// Format large numbers to readable format (e.g., 1.5M, 2.3B)
export function formatNumber(num: number): string {
  if (num >= 1e9) {
    return `${(num / 1e9).toFixed(1)}B`
  }
  if (num >= 1e6) {
    return `${(num / 1e6).toFixed(1)}M`
  }
  if (num >= 1e3) {
    return `${(num / 1e3).toFixed(1)}K`
  }
  return num.toFixed(2)
}

// Calculate percentage change
export function calculatePercentageChange(current: number, previous: number): number {
  return ((current - previous) / previous) * 100
}

// Generate mock trading data
export function generateMockData(basePrice: number, count: number = 24) {
  const data = []
  let currentPrice = basePrice

  for (let i = 0; i < count; i++) {
    const timestamp = Date.now() - (count - i) * 3600000 // hourly data
    const change = (Math.random() - 0.5) * (basePrice * 0.02) // 2% max change
    currentPrice += change

    data.push({
      timestamp,
      open: currentPrice,
      high: currentPrice + Math.random() * (basePrice * 0.01),
      low: currentPrice - Math.random() * (basePrice * 0.01),
      close: currentPrice + (Math.random() - 0.5) * (basePrice * 0.01),
      volume: Math.random() * 1000 + 500
    })
  }

  return data
}

// Analyze price movement and generate trading signals
export function analyzePriceMovement(
  currentPrice: number,
  previousPrice: number,
  confidence: number
): {
  recommendation: 'buy' | 'sell' | 'hold'
  stopLoss: number
  takeProfit: number
  confidence: number
} {
  const priceChange = calculatePercentageChange(currentPrice, previousPrice)
  const volatility = Math.abs(priceChange)
  
  // Adjust confidence based on volatility
  let newConfidence = confidence + (Math.random() - 0.5) * 5
  newConfidence = Math.max(0, Math.min(100, newConfidence))

  // Generate trading signals
  if (Math.abs(priceChange) < 0.1) {
    return {
      recommendation: 'hold',
      stopLoss: currentPrice * 0.98,
      takeProfit: currentPrice * 1.02,
      confidence: newConfidence
    }
  }

  if (priceChange > 0) {
    return {
      recommendation: 'buy',
      stopLoss: currentPrice * 0.99,
      takeProfit: currentPrice * 1.03,
      confidence: newConfidence
    }
  }

  return {
    recommendation: 'sell',
    stopLoss: currentPrice * 1.01,
    takeProfit: currentPrice * 0.97,
    confidence: newConfidence
  }
}

// Format price with appropriate decimals and currency symbol
export function formatPrice(price: number, currency: string = '$'): string {
  return `${currency}${price.toFixed(2)}`
}

// Calculate simple moving average (SMA)
export function calculateSMA(prices: number[], period: number): number[] {
  const sma = []
  for (let i = period - 1; i < prices.length; i++) {
    const sum = prices.slice(i - period + 1, i + 1).reduce((a, b) => a + b, 0)
    sma.push(sum / period)
  }
  return sma
}

// Calculate Relative Strength Index (RSI)
export function calculateRSI(prices: number[], period: number = 14): number[] {
  const rsi = []
  const changes = prices.slice(1).map((price, i) => price - prices[i])
  const gains = changes.map(change => change > 0 ? change : 0)
  const losses = changes.map(change => change < 0 ? -change : 0)

  let avgGain = gains.slice(0, period).reduce((a, b) => a + b, 0) / period
  let avgLoss = losses.slice(0, period).reduce((a, b) => a + b, 0) / period

  for (let i = period; i < prices.length; i++) {
    const rs = avgGain / avgLoss
    rsi.push(100 - (100 / (1 + rs)))

    avgGain = (avgGain * (period - 1) + gains[i]) / period
    avgLoss = (avgLoss * (period - 1) + losses[i]) / period
  }

  return rsi
}
