'use client'

import { useState, useEffect } from 'react'
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"

interface NewsItem {
  id: number
  title: string
  summary: string
  timestamp: string
  impact: 'high' | 'medium' | 'low'
  category: 'market' | 'crypto' | 'regulation' | 'technology'
  source: string
}

export function TradingNews() {
  const [news, setNews] = useState<NewsItem[]>([])

  // Mock news data - in a real app, this would come from an API
  useEffect(() => {
    const mockNews: NewsItem[] = [
      {
        id: 1,
        title: "Bitcoin ETF Sees Record Inflows",
        summary: "Institutional investors pour $2.1B into Bitcoin ETFs this week, signaling strong institutional adoption.",
        timestamp: "2 hours ago",
        impact: "high",
        category: "crypto",
        source: "CoinDesk"
      },
      {
        id: 2,
        title: "Fed Hints at Rate Cut in Q2",
        summary: "Federal Reserve officials suggest potential interest rate cuts could boost risk assets including crypto.",
        timestamp: "4 hours ago",
        impact: "high",
        category: "market",
        source: "Reuters"
      },
      {
        id: 3,
        title: "Major Exchange Lists New DeFi Token",
        summary: "Binance announces listing of emerging DeFi protocol, causing 15% price surge in pre-market trading.",
        timestamp: "6 hours ago",
        impact: "medium",
        category: "crypto",
        source: "CryptoNews"
      },
      {
        id: 4,
        title: "EU Finalizes Crypto Regulation Framework",
        summary: "European Union completes MiCA implementation guidelines, providing clarity for crypto businesses.",
        timestamp: "8 hours ago",
        impact: "medium",
        category: "regulation",
        source: "Bloomberg"
      },
      {
        id: 5,
        title: "Whale Movement Alert",
        summary: "Large Bitcoin holder moves 10,000 BTC to unknown wallet, sparking market speculation.",
        timestamp: "12 hours ago",
        impact: "low",
        category: "crypto",
        source: "Whale Alert"
      }
    ]
    setNews(mockNews)
  }, [])

  const getImpactColor = (impact: string) => {
    switch (impact) {
      case 'high':
        return 'bg-red-500/20 text-red-400 border-red-500/30'
      case 'medium':
        return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30'
      case 'low':
        return 'bg-green-500/20 text-green-400 border-green-500/30'
      default:
        return 'bg-gray-500/20 text-gray-400 border-gray-500/30'
    }
  }

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'crypto':
        return 'bg-blue-500/20 text-blue-400'
      case 'market':
        return 'bg-purple-500/20 text-purple-400'
      case 'regulation':
        return 'bg-orange-500/20 text-orange-400'
      case 'technology':
        return 'bg-cyan-500/20 text-cyan-400'
      default:
        return 'bg-gray-500/20 text-gray-400'
    }
  }

  return (
    <Card className="p-6 bg-[#0B1217] border-0 rounded-xl">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-xl font-semibold text-[#F0F3F6]">Trading News</h2>
        <span className="text-sm text-gray-400">Live market updates</span>
      </div>
      
      <div className="space-y-4 max-h-[600px] overflow-y-auto">
        {news.map((item) => (
          <div 
            key={item.id}
            className="bg-[#1E2329] border border-gray-800 rounded-lg p-4 hover:bg-[#252A32] transition-colors cursor-pointer group"
          >
            <div className="flex items-start justify-between mb-3">
              <div className="flex items-center gap-2">
                <Badge className={`text-xs ${getCategoryColor(item.category)}`}>
                  {item.category.toUpperCase()}
                </Badge>
                <Badge className={`text-xs ${getImpactColor(item.impact)}`}>
                  {item.impact.toUpperCase()} IMPACT
                </Badge>
              </div>
              <span className="text-xs text-gray-500">{item.timestamp}</span>
            </div>
            
            <h3 className="text-[#F0F3F6] font-medium mb-2 group-hover:text-white transition-colors">
              {item.title}
            </h3>
            
            <p className="text-sm text-gray-400 leading-relaxed mb-3">
              {item.summary}
            </p>
            
            <div className="flex items-center justify-between">
              <span className="text-xs text-gray-500">Source: {item.source}</span>
              <div className="flex items-center text-xs text-gray-500">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-3 w-3 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                </svg>
                Read more
              </div>
            </div>
          </div>
        ))}
      </div>
      
      <div className="mt-4 pt-4 border-t border-gray-800">
        <button className="w-full text-center text-sm text-gray-400 hover:text-gray-300 transition-colors">
          View all news 
        </button>
      </div>
    </Card>
  )
}
