'use client'

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface SignalData {
  recommendation: 'buy' | 'sell' | 'hold'
  stopLoss: number
  takeProfit: number
  confidence: number
  price: number
}

interface SignalPanelProps {
  data: SignalData
}

export function SignalPanel({ data }: SignalPanelProps) {
  const getRecommendationColor = (rec: string) => {
    switch (rec.toLowerCase()) {
      case 'buy':
        return 'text-green-500'
      case 'sell':
        return 'text-red-500'
      default:
        return 'text-gray-400'
    }
  }

  const calculatePotentialProfit = () => {
    if (data.recommendation === 'buy') {
      return ((data.takeProfit - data.price) / data.price * 100).toFixed(2)
    } else {
      return ((data.price - data.takeProfit) / data.price * 100).toFixed(2)
    }
  }

  return (
    <Card className="p-4 bg-[#1E2329] border-0">
      <h3 className="text-lg font-bold text-white mb-4">Trading Signals</h3>
      <div className="space-y-4">
        {/* Signal Strength Indicator */}
        <div className="p-4 bg-[#161B22] rounded-lg">
          <div className="flex justify-between items-center mb-2">
            <p className="text-sm text-gray-400">Signal Strength</p>
            <span className="text-sm font-bold text-white">{data.confidence}%</span>
          </div>
          <div className="w-full bg-gray-700 rounded-full h-2">
            <div 
              className="bg-[#3861FB] h-2 rounded-full transition-all duration-500"
              style={{ width: `${data.confidence}%` }}
            />
          </div>
        </div>

        {/* Recommendation */}
        <div className="p-4 bg-[#161B22] rounded-lg">
          <p className="text-sm text-gray-400 mb-2">Recommendation</p>
          <p className={`text-xl font-bold ${getRecommendationColor(data.recommendation)}`}>
            {data.recommendation.toUpperCase()}
          </p>
        </div>

        {/* Entry Price */}
        <div className="p-4 bg-[#161B22] rounded-lg">
          <p className="text-sm text-gray-400 mb-2">Current Price</p>
          <p className="text-xl font-bold text-white">${data.price.toFixed(2)}</p>
        </div>

        {/* Stop Loss */}
        <div className="p-4 bg-[#161B22] rounded-lg">
          <p className="text-sm text-gray-400 mb-2">Stop Loss</p>
          <p className="text-xl font-bold text-red-500">${data.stopLoss.toFixed(2)}</p>
          <p className="text-sm text-gray-400">
            Potential Loss: {((Math.abs(data.stopLoss - data.price) / data.price) * 100).toFixed(2)}%
          </p>
        </div>

        {/* Take Profit */}
        <div className="p-4 bg-[#161B22] rounded-lg">
          <p className="text-sm text-gray-400 mb-2">Take Profit</p>
          <p className="text-xl font-bold text-green-500">${data.takeProfit.toFixed(2)}</p>
          <p className="text-sm text-gray-400">
            Potential Profit: {calculatePotentialProfit()}%
          </p>
        </div>

        {/* Action Buttons */}
        <div className="grid grid-cols-2 gap-2">
          <Button 
            className="w-full bg-green-500 text-white border-0 hover:bg-green-600"
            disabled={data.recommendation !== 'buy'}
          >
            Buy Now
          </Button>
          <Button 
            className="w-full bg-red-500 text-white border-0 hover:bg-red-600"
            disabled={data.recommendation !== 'sell'}
          >
            Sell Now
          </Button>
        </div>
      </div>
    </Card>
  )
}
