'use client'

import { useState, useEffect, useMemo } from 'react'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { generateMockData } from '@/lib/trading-utils'
import { PriceDisplay } from './PriceDisplay'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
} from 'chart.js'
import { Line } from 'react-chartjs-2'

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
)

interface CandleData {
  timestamp: number
  open: number
  high: number
  low: number
  close: number
  volume: number
}

interface TradingChartProps {
  data?: CandleData[]
}

export function TradingChart({ data }: TradingChartProps) {
  const [timeframe, setTimeframe] = useState<'1H' | '4H' | '1D' | '1W'>('1D')
  const [priceChange, setPriceChange] = useState<number>(0)

  // Generate mock data if none provided
  const chartData = useMemo(() => data || generateMockData(50000), [data])

  // Calculate price change
  useEffect(() => {
    if (chartData.length > 0) {
      const firstPrice = chartData[0].close
      const lastPrice = chartData[chartData.length - 1].close
      const change = ((lastPrice - firstPrice) / firstPrice) * 100
      setPriceChange(change)
    }
  }, [chartData])

  const formatTooltipPrice = (price: number) => {
    if (price >= 1000) {
      return `$${(price / 1000).toFixed(2)}K`
    }
    return `$${price.toFixed(2)}`
  }

  const formatTooltipTime = (time: string) => {
    const date = new Date(time)
    return date.toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit',
      hour12: true
    })
  }

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    animation: {
      duration: 0
    },
    interaction: {
      intersect: false,
      mode: 'index' as const
    },
    scales: {
      x: {
        border: {
          display: false
        },
        grid: {
          color: 'rgba(255, 255, 255, 0.05)',
          drawTicks: false
        },
        ticks: {
          color: '#666666',
          font: {
            size: 11
          },
          maxRotation: 0
        }
      },
      y: {
        position: 'right' as const,
        border: {
          display: false
        },
        grid: {
          color: 'rgba(255, 255, 255, 0.05)',
          drawTicks: false
        },
        ticks: {
          color: '#666666',
          font: {
            size: 11
          },
          callback: (value: number | string) => {
            if (typeof value === 'number') {
              return `$${value.toFixed(2)}`
            }
            return value
          },
          padding: 8
        }
      }
    },
    plugins: {
      legend: {
        display: false
      },
      tooltip: {
        mode: 'index' as const,
        intersect: false,
        backgroundColor: 'rgba(11, 18, 23, 0.95)',
        titleColor: '#666666',
        bodyColor: '#F0F3F6',
        borderColor: 'rgba(255, 255, 255, 0.1)',
        borderWidth: 1,
        padding: { x: 12, y: 8 },
        titleFont: { size: 12 },
        bodyFont: { size: 13 },
        displayColors: false,
        callbacks: {
          title: (tooltipItems: any) => {
            return `Time: ${formatTooltipTime(tooltipItems[0].label)}`;
          },
          label: (context: any) => {
            const value = context.parsed.y;
            if (typeof value === 'number') {
              return `Price: ${formatTooltipPrice(value)}`;
            }
            return `Price: ${value}`;
          }
        }
      }
    }
  }

  const chartDataConfig = {
    labels: chartData.map((_, index) => `${index + 1}h`),
    datasets: [
      {
        label: 'Price',
        data: chartData.map(candle => candle.close),
        backgroundColor: 'rgba(22, 199, 132, 0.1)',
        borderColor: '#16c784',
        borderWidth: 1.5,
        tension: 0.3,
        fill: true,
        pointRadius: 0,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: '#16c784',
        pointHoverBorderColor: '#16c784',
        pointHoverBorderWidth: 2
      }
    ]
  }

  return (
    <Card className="p-4 bg-[#0B1217] border-0 rounded-xl">
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-3">
          <h3 className="text-base font-semibold text-[#F0F3F6]">Price Chart</h3>
          <PriceDisplay 
            price={chartData[chartData.length - 1]?.close || 0}
            change={priceChange}
          />
        </div>
        <div className="flex gap-1">
          {(['1H', '4H', '1D', '1W'] as const).map((tf) => (
            <Button
              key={tf}
              variant="outline"
              size="sm"
              onClick={() => setTimeframe(tf)}
              className={`${
                timeframe === tf
                  ? 'bg-[#16c784] text-white border-0 hover:bg-[#16c784]/90'
                  : 'text-[#666666] hover:text-white bg-transparent border-0 hover:bg-[#1E2329]'
              } text-xs px-3 py-1 h-7 rounded-md transition-colors`}
            >
              {tf}
            </Button>
          ))}
        </div>
      </div>
      <div className="h-[400px] bg-[#0B1217] rounded-lg p-4">
        <Line options={chartOptions} data={chartDataConfig} />
      </div>
    </Card>
  )
}
