type SignalType = {
  recommendation: 'buy' | 'sell' | 'hold'
  stopLoss: number
  takeProfit: number
  confidence: number
  price: number
}

type DataType = {
  price: number
  change24h: number
  volume24h: string
  marketCap: string
  signals: SignalType
}

